/*error*/
.aws-btn--progress .aws-btn__content:after, .aws-btn--progress .aws-btn__progress, .aws-btn .aws-btn__content:after, .aws-btn .aws-btn__wrapper:after, .aws-btn .aws-btn__wrapper:before {
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}
.aws-btn {
    padding: 0;
    margin: 0;
}
.aws-btn:focus, .aws-btn {
    outline-color: 0;
    outline-style: none;
    outline-width: 0;
}
.aws-btn {
    -webkit-user-select: none;
    -moz-user-select: none;
    user-select: none;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}


.aws-btn .aws-btn__content>span:nth-child(1) {
    display: flex;
    align-items: center;
    transform-style: preserve-3d;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    transform: skew(0) translate3d(0,  0,  0);
}
.aws-btn .aws-btn__content>span:nth-child(1)>svg:only-child {
    margin: 0;
}
.aws-btn .aws-btn__content>svg:nth-child(1) {
    margin-right: 4px;
}
.aws-btn .aws-btn__content>svg:nth-last-child(1) {
    margin-left: 4px;
}
.aws-btn:before {
    content: " ";
    background-color: var(--button-shadow-color);
    width: calc(100% - 2px);
    height: calc(100% - var(--button-raise-level)*var(--button-shadow-divisor));
    bottom: calc(0px - var(--button-raise-level)/var(--button-shadow-divisor));
    left: 1px;
    position: absolute;
    border-radius: var(--button-default-border-radius);
    transform: skewY(0) translate3d(0,  0,  0);
    transition: transform calc(var(--transform-speed)*.8) ease-out, background calc(var(--transform-speed)*.8) ease-out;
}
